                <v-card-actions class="pa-0" style="border-top: 1px solid lightgray;">
                    <v-btn x-large class="ml-0 after_select_row d-none" text style="width:50%;" @click="changeImgSubmit">
                        <b class="success--text">적용</b>
                    </v-btn>
                    <v-btn x-large class="ml-0 after_select_row d-none" text style="width:50%;" @click="closeDialog(true)">
                        <b>취소</b>
                    </v-btn>

                    <v-btn x-large class="ml-0 mobile_select_row" text style="width:100%;" @click="closeDialog">
                        <b>취소</b>
                    </v-btn>
