<style lang="scss">
@import "@/assets/scss/views/index.scss";
</style>

<script>
// import { mapGetters } from "vuex";
import vprogress from '@/components/common/Progress';
import {bus} from '@/main'
import {mapState} from 'vuex';

export default {
    name: "index",
    components: {
        vprogress
    },
    data: () => ({
      dense: false,
      fixedHeader: false,
      height: 300,
      sel_list: ['Today', 'Week', 'Year'],

      uname : '',
      cname : '',
      now_users_cnt : 0,

      file_path : '',
      logofile: '',
    }),

    created : function () {
        this.cname = this.$storage.get('user').cname;
        this.uname = this.$storage.get('user').name;
        this.now_users_cnt = this.$storage.get('user').now_users_cnt;
        this.logofile = `${this.$host}` + '/images/selpic/' + this.$storage.get('user').logofile;
    },
    mounted (){

        window.addEventListener('message', (e) => {
            if( e.data.action == "preview" ){
                if( this.$storage.get('user').id == e.data.uid ){
                    let msr = document.getElementsByClassName('mobile_select_row');
                    let asr = document.getElementsByClassName('after_select_row');

                    for( let [idx, item] of Object.entries(msr) ){
                        item.classList.remove('d-flex');
                        item.classList.add('d-none');
                    }

                    for( let [idx, item] of Object.entries(asr) ){
                        item.classList.remove('d-none');
                        item.classList.add('d-block');
                    }

                    this.file_path = e.data.file_path;
                }
            }
            else if( e.data.action == 'change_success' ){

                let json = this.$storage.get('user');
                json.logofile = e.data.file_path;
                this.$storage.set('user', json);

                this.logofile = e.data.file_path;
                this.closeDialog(true);
            }

        });

        bus.$on('close_event', (data) => {
            if(data.status == true){
                let msr = document.getElementsByClassName('mobile_select_row');
                let asr = document.getElementsByClassName('after_select_row');

                for( let [idx, item] of Object.entries(msr) ){
                    item.classList.remove('d-none');
                    item.classList.add('d-flex');
                }

                for( let [idx, item] of Object.entries(asr) ){
                    item.classList.remove('d-block');
                    item.classList.add('d-none');
                }
            }
        });

    },
    methods : {
        camera : function () {
            window.parent.postMessage({
                action: 'camera',
                uid: this.$storage.get('user').id
            }, '*');
        },

        filePicker : function () {
            window.parent.postMessage({
                action: 'filepicker',
                uid: this.$storage.get('user').id
            }, '*');
        },
        changeInfo : function (type) {
            this.openDialog('change_' + type);
        },
        changeImgSubmit () {
            window.parent.postMessage({
                action: 'cameraSubmit',
                api: '/user/' + this.$storage.get('user').academy_id + '/' + this.$storage.get('user').id + '/changeimg',
                uid: this.$storage.get('user').id
            }, '*');
        }

    },
    computed : {
        ...mapState([
            // 'uname', 'cname', 'now_users_cnt'
        ]),
    }
};
</script>
